<template>
    <div class="item">
        <v-tooltip class="item__tooltip" @click="vModelValue = !vModelValue">
            <slot name="title"></slot>

            <template #text>
                <slot name="tooltip"></slot>
            </template>
        </v-tooltip>

        <switches
            v-model="vModelValue"
            theme="custom"
            color="blue"
            class="tabs__switcher"
        />
    </div>
</template>

<script>
import VTooltip from '@/components/ui/VTooltip.vue';
import Switches from 'vue-switches';

import '@/assets/css/vue-switches.css';

export default {
    components: {
        VTooltip,
        Switches,
    },

    props: {
        value: {
            type: [Number, Boolean],
            required: true,
        },
    },

    data() {
        return {
            vModelValue: this.value,
        };
    },

    watch: {
        vModelValue(newValue) {
            this.$emit('change', newValue);
        },
    },
};
</script>

<style lang="css" scoped>
.item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    width: 100%;
}

.item__switcher {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>
