<template>
    <div class="item">
        <v-tooltip class="item__tooltip">
            <slot name="title"></slot>

            <template #text>
                <slot name="tooltip"></slot>
            </template>
        </v-tooltip>

        <v-dropdown
            v-model="vModelValue"
            class="item__dropdown"
            :options="options"
        />
    </div>
</template>

<script>
import VTooltip from '@/components/ui/VTooltip.vue';
import VDropdown from '@/components/ui/VDropdown.vue';

export default {
    components: {
        VTooltip,
        VDropdown,
    },

    props: {
        value: {
            type: [Number, Boolean],
            required: true,
        },

        options: {
            type: Array,
            required: true,
        },
    },

    data() {
        return {
            vModelValue: this.value,
        };
    },

    watch: {
        vModelValue(newValue) {
            this.$emit('change', newValue);
        },
    },
};
</script>

<style lang="css" scoped>
.item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    width: 100%;
}

.item__switcher {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>
