        <tab :prefix="settingsIcon" name="Настройки игры" :suffix="chevronIcon">
            <v-settings-card class="tabs__card">
                <template #title> Демо страницы </template>

                <div class="tabs__item">
                    <router-link class="tabs__link" to="popup-demo">
                        Модальные окна
                    </router-link>
                </div>

                <div class="tabs__item">
                    <router-link class="tabs__link" to="loading">
