                    </router-link>
                </div>
            </v-settings-card>
        </tab>

        <tab
            :prefix="filterIcon"
            name="Параметры запуска"
            :suffix="chevronIcon"
        >
            <v-settings-card class="tabs__card">
                <template #title> Графика </template>

                <v-tabs-item-switch
                    class="tabs__item"
                    :value="displayMode"
                    @change="displayMode = $event"
                >
                    <template #title> Режим отображения </template>

                    <template #tooltip>
                        Тут пишем описание данной функции желательно не слишком
                        большое.
                    </template>
                </v-tabs-item-switch>

                <v-tabs-item-dropdown
                    class="tabs__item"
                    :value="resolutionID"
                    :options="resolutionOptions"
                    @change="resolutionID = $event"
                >
                    <template #title> Разрешение </template>

                    <template #tooltip>
                        Тут пишем описание данной функции желательно не слишком
                        большое.
                    </template>
                </v-tabs-item-dropdown>
            </v-settings-card>

            <v-settings-card class="tabs__card">
                <template #title> Расширенные настройки изображения </template>

                <v-tabs-item-switch
                    class="tabs__item"
                    :value="shadowsQuality"
                    @change="shadowsQuality = $event"
                >
                    <template #title> Общее качество теней </template>

                    <template #tooltip>
                        Тут пишем описание данной функции желательно не слишком
                        большое.
                    </template>
                </v-tabs-item-switch>

                <v-tabs-item-switch
                    class="tabs__item"
                    :value="textureDetails"
                    @change="textureDetails = $event"
                >
                    <template #title> Детализация моделей и текстур </template>

                    <template #tooltip>
                        Тут пишем описание данной функции желательно не слишком
                        большое.
                    </template>
                </v-tabs-item-switch>

                <v-tabs-item-switch
                    class="tabs__item"
                    :value="effectsDetails"
                    @change="effectsDetails = $event"
                >
                    <template #title> Детализация эффектов</template>

                    <template #tooltip>
                        Тут пишем описание данной функции желательно не слишком
                        большое.
                    </template>
                </v-tabs-item-switch>
            </v-settings-card>
        </tab>

        <tab :prefix="windowsIcon" name="Система" :suffix="chevronIcon">
            Third tab content
        </tab>

        <tab
            :prefix="lightningIcon"
            name="Настройка FPS Booster"
            :suffix="chevronIcon"
        >
            Fourth tab content
        </tab>
    </tabs>
</template>

<script>
import VSettingsCard from '@/components/VSettingsCard.vue';
import VTabsItemSwitch from '@/components/VTabsItemSwitch.vue';
import VTabsItemDropdown from '@/components/VTabsItemDropdown.vue';

export default {
    components: {
        VSettingsCard,

        VTabsItemSwitch,
        VTabsItemDropdown,
    },

    computed: {
        resolutionOptions: {
            get() {
                const { resolutions } = this.$store.getters[
                    'settings/getSettingsOptions'
                ];

                return resolutions;
            },
        },

        displayMode: {
            get() {
                return this.getSettingByKey('displayMode');
            },

            set(value) {
                this.setSettingsByKey('displayMode', value);
            },
        },

        shadowsQuality: {
            get() {
                return this.getSettingByKey('shadowsQuality');
            },

            set(value) {
                this.setSettingsByKey('shadowsQuality', value);
            },
        },

        textureDetails: {
            get() {
                return this.getSettingByKey('textureDetails');
            },

            set(value) {
                this.setSettingsByKey('textureDetails', value);
            },
        },

        effectsDetails: {
            get() {
                return this.getSettingByKey('effectsDetails');
            },

            set(value) {
                this.setSettingsByKey('effectsDetails', value);
            },
        },

        resolutionID: {
            get() {
                return this.getSettingByKey('resolutionID');
            },

            set(value) {
                this.setSettingsByKey('resolutionID', value);
            },
        },

        settingsIcon() {
            return this.getIconTemplate('settings');
        },

        filterIcon() {
            return this.getIconTemplate('filter');
        },

        windowsIcon() {
            return this.getIconTemplate('windows');
        },

        lightningIcon() {
            return this.getIconTemplate('lightning');
        },

        chevronIcon() {
            return '<span class="tabs__chevron"></span>';
        },
    },

    methods: {
        getSettingByKey(key) {
            return this.$store.getters['settings/getSettingsByKey'](key);
        },

        setSettingsByKey(key, newValue) {
            const valueinStore = this.getSettingByKey(key);

            if (valueinStore === newValue) return;

            this.$store.dispatch('settings/setSettingsByKey', {
                key,
                value: newValue,
            });
            this.$store.dispatch('settings/sendSettingsToServer');
        },

        getIconTemplate(icon) {
            return `<span class='tabs__icon tabs__icon_image_${icon}'></span>`;
        },
    },
};
</script>

